export default async function InitCalandar() {
  const today = new Date();
  let curYear = today.getFullYear();
  let curMonth = today.getMonth();
  let moveMonth;
  await renderCalendar(today);

  async function renderCalendar(thisMonth) {
    const currentYear = thisMonth.getFullYear();
    const currentMonth = thisMonth.getMonth();

    // ì´ì „ ë‹¬ì˜ ë§ˆì§€ë§‰ ë‚  ë‚ ì§œì™€ ìš”ì¼ êµ¬í•˜ê¸°
    // ë‚ ì§œë¥¼ 0ìœ¼ë¡œ ì§€ì •í•˜ë©´ ì €ë²ˆ ë‹¬ì˜ ë§ˆì§€ë§‰ ë‚ ì§œë¥¼ ê°€ì§„ date ê°ì²´ê°€ ë°˜í™˜ë¨
    const startDay = new Date(currentYear, currentMonth, 0);
    const prevDate = startDay.getDate();
    const prevDay = startDay.getDay();

    // ì´ë²ˆ ë‹¬ì˜ ë§ˆì§€ë§‰ ë‚  ë‚ ì§œì™€ ìš”ì¼ êµ¬í•˜ê¸°
    const endDay = new Date(currentYear, currentMonth + 1, 0);
    const nextDate = endDay.getDate();
    const nextDay = endDay.getDay();

    setCalender();

    function setCalender() {
      // ë‹¬ë ¥ ë Œë”ë§
      const thisMon = document.querySelector(".days");
      thisMon.innerHTML = "";

      // ì§€ë‚œ ë‹¬ ë Œë”ë§
      // ì´ë²ˆ ë‹¬ ì‹œì‘ ìš”ì¼ì´ ì¼ìš”ì¼ì´ë©´ ì§€ë‚  ë‹¬ ì¶œë ¥ X
      if (prevDay != 6) {
        for (let i = prevDate - prevDay; i <= prevDate; i++) {
          thisMon.innerHTML =
            thisMon.innerHTML + `<div class="day disable">${i}</div>`;
        }
      }
      // ì´ë²ˆ ë‹¬ ë Œë”ë§
      for (let i = 1; i <= nextDate; i++) {
        thisMon.innerHTML = thisMon.innerHTML + `<div class="day">${i}</div>`;
      }

      // ë‹¤ìŒ ë‹¬ ë Œë”ë§
      if (nextDay !== 6) {
        for (let i = 1; i < 7 - nextDay; i++) {
          thisMon.innerHTML =
            thisMon.innerHTML + `<div class="day disable">${i}</div>`;
        }
      }

      // í˜„ì¬ ì›” í‘œê¸°
      const month = document.querySelector(".month");
      month.textContent = `${currentYear}.${currentMonth + 1}`;

      // ì˜¤ëŠ˜ ë‚ ì§œ í‘œê¸°
      if (
        today.getMonth() === currentMonth &&
        today.getFullYear() === currentYear
      ) {
        const todayDate = today.getDate();
        const currentMonthDate = document.querySelectorAll(".day");
        currentMonthDate[todayDate - 1].classList.add("today");
      }
    }
  }
  // prev ë²„íŠ¼ ëˆ„ë¥´ë©´ ì§€ë‚œ ë‹¬ë¡œ ì´ë™
  const prevBtn = document.querySelector(".select-month .prev");
  const loadingEl = document.querySelector(".loading");
  let isRunning = false;

  prevBtn.addEventListener("click", async function () {
    if (isRunning) return;
    isRunning = true;
    curMonth = curMonth - 1;
    moveMonth = new Date(curYear, curMonth, 1);
    await renderCalendar(moveMonth);
    isRunning = false;
  });
  // next ë²„íŠ¼ ëˆ„ë¥´ë©´ ë‹¤ìŒ ë‹¬ë¡œ ì´ë™
  const nextBtn = document.querySelector(".select-month .next");
  nextBtn.addEventListener("click", async function () {
    if (isRunning) return;
    isRunning = true;
    curMonth = curMonth + 1;
    moveMonth = new Date(curYear, curMonth, 1);
    await renderCalendar(moveMonth);
    isRunning = false;
  });
}
